@use "variables" as *;
@use "sass:meta";

@mixin mq($device) {
  //   'px'   max-width  
  @if meta.type-of($device) == 'number' {
    @media (max-width: #{$device}px) { @content; }
  }

  @if $device == pc-xlarge {
    @media (min-width: 1921px) { @content; }
  } @else if $device == pc-large {
    @media (max-width: #{$breakpoint-pc-large}) { @content; }
  } @else if $device == pc {
    @media (max-width: #{$breakpoint-pc}) { @content; } //  
  } @else if $device == tablet {
    @media (max-width: #{$breakpoint-tablet}) { @content; } //  
  } @else if $device == tablet-small {
    @media (max-width: #{$breakpoint-tablet-small}) { @content; } //  
  } @else if $device == mobile {
    @media (max-width: #{$breakpoint-mobile}) { @content; } //  
  } @else if $device == mobile-medium {
    @media (max-width: #{$breakpoint-mobile-medium}) { @content; } // 
  } @else if $device == mobile-small {
    @media (max-width: #{$breakpoint-mobile-small}) { @content; } //  
  }
}