@charset "UTF-8";
@use "sass:list";
@use "./util" as *;
@use "./base" as *;
@use "./component" as *;
@use "./layout" as *;
// @forward "./test.scss";

@forward "./page";

.inner {
  margin-left: auto;
  margin-right: auto;
  padding: 0 var(--#{$prefix}-content-padding-x);
  max-width: var(--#{$prefix}-content-wrap-width);
  width: 100%;
}

.contents {
  a {
    display: inline-block;
  }
}

// section .inner {
